:root {
  --iq-title-text: #3f414d;
  --iq-body-text: #777D74;
  --iq-primary: #50b5ff;
  --iq-secondary: #a09e9e;
  --iq-success: #49f0d3;
  --iq-warning: #ffba68;
  --iq-danger: #ff9b8a;
  --iq-info: #d592ff;
  --iq-dark: #60658a;
  --iq-light: #fafafb;
  --iq-white: #ffffff;
  --iq-black: #000000;
  --iq-light-primary: #f5fbff;
  --iq-light-secondary: #f1f2f1;
  --iq-light-success: #eefff2;
  --iq-light-danger: #fff5f4;
  --iq-light-warning: #fffcf3;
  --iq-light-info: #eee6f5;
  --iq-light-dark: #f0efef;
  --iq-primary-hover: #2aa3fb;
  --iq-border-light: #f1f1f1;
  --iq-dark-primary: #29324f;
  --iq-dark-secondary: rgba(160,158,158,0.2);
  --iq-dark-success: rgba(73,240,211,0.2);
  --iq-dark-danger: rgba(255,155,138,0.2);
  --iq-dark-warning: rgba(255,186,104,0.2);
  --iq-dark-info: rgba(213,146,255,0.2);
  --iq-primary-hover: #2aa3fb;
  --iq-border-light: #f1f1f1;
  --iq-dark-bg: #182039;
  --iq-dark-box: #1e2745;
  --iq-dark-body-text: #8c91b6;
  --iq-dark-title-text: #8c91b6;
  --iq-border-dark: #252e4b;
}



/* scroller */
.scroller { overflow-y: auto; scrollbar-color: var(--iq-primary) var(--iq-dark-primary); scrollbar-width: thin; }
.scroller::-webkit-scrollbar-thumb { background-color: var(--iq-primary); }
.scroller::-webkit-scrollbar-track { background-color: var(--iq-dark-primary); }
#sidebar-scrollbar { overflow-y: auto; scrollbar-color: var(--iq-primary) var(--iq-dark-primary); scrollbar-width: thin; }
#sidebar-scrollbar::-webkit-scrollbar-thumb { background-color: var(--iq-primary); }
#sidebar-scrollbar::-webkit-scrollbar-track { background-color: var(--iq-dark-primary); }
/*---------------------------------------------------------------------
Map Profile
-----------------------------------------------------------------------*/
.dash-tracking-icon { height: 80px; }
.map-profile-card { margin-right: 30px; border: 1px solid var(--iq-border-dark); margin-bottom: 15px; }
.track { display: -webkit-inline-box; display: -moz-inline-box; overflow-x: scroll; }
::-webkit-scrollbar { width: 8px; height: 8px; border-radius: 20px; }

/* Track */
::-webkit-scrollbar-track { background: var(--iq-border-dark); border-radius: 20px; }

/* Handle */
::-webkit-scrollbar-thumb { background: var(--iq-secondary); border-radius: 20px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover { background: var(--iq-dark); border-radius: 20px; }



